.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #141943;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.srisuvit_logo {
  position: absolute;
  top: 0;
  right: 0;
  width: 40vmin;
}

.tower {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 80vmin;
}

.rocket {
  position: absolute;
  top: 0;
  left: 0;
  width: 30vmin;
}

.srisuvit {
  width: 70vmin;
  z-index: 10;
}

.score {
  width: 70vmin;
  z-index: 10;
}

.player_name, .player_id, .answers {
  width: 70vmin;
  font-size: 6vmin;
  padding: 3vmin;
  border-radius: 12px;
  border: solid 2px orange;
  z-index: 10;
  color: orange;
}

::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: orange;
  opacity: 1; /* Firefox */
}

::-ms-input-placeholder { /* Internet Explorer 10-11 */
  color: orange;
}

::-ms-input-placeholder { /* Microsoft Edge */
  color: orange;
}

.button_start, .button_ans {
  background-repeat: no-repeat;
  width: 40vmin;
  height: 14vmin;
  background-size: 100%;
  border: none;
  background-color: transparent;
  cursor: pointer;
  z-index: 10;
}

.countdown {
  background-color: orange;
  border-radius: 10vmin;
  width: 50vmin;
  height: 17vmin;
  z-index: 10;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.time {
  align-self: center;
  font-size: 12vmin;
  font-weight: bold;
  font-family: Oswald;
}

.question {
  background-color: rgba(255, 255, 255, 0.1);
  width: 80vmin;
  height: 30vmin;
  border-radius: 2vmin;
}

.question_span {
  font-family: Aileron-Bold;
  background-color: rgba(255, 255, 255, 0.1);
  width: 70vmin;
  height: 30vmin;
  border-radius: 2vmin;
  padding: 3vmin;
  font-size: 5vmin;
  overflow-x: scroll;
  text-align: left;
  z-index: 10;
}

.palyer_id_span {
  font-family: Aileron-Bold;
  font-weight: bold;
  font-size: 6vmin;
  background-color: orange;
  padding: 4vmin;
  width: 50vmin;
  border-radius: 10vmin;
  z-index: 10;
}

.score_span {
  font-family: Aileron-Bold;
  font-weight: bold;
  font-size: 15vmin;
  background-color: orange;
  width: 50vmin;
  padding: 10vmin;
  border-radius: 3vmin;
  z-index: 10;
}